@import '../../mixins/mixin-main.scss';
.main-header {
    position: sticky;
    top: 0;
    z-index: 9;
    padding: 1.875rem 2.5rem;
}

.main-header_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.main-header_wrapper-logo {
    max-width: 16.5rem;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.main-header_wrapper-menu {
    ul {
        list-style: none;
        display: flex;
        align-items: center;
        a {
            text-decoration: none;
            color: $black;
        }
    }
    li {
        &:not(:first-of-type) {
            margin-left: 1.5rem;
        }
        position: relative;
        &::after {
            content: '';
            position: absolute;
            bottom: -.5rem;
            left: 0;
            width: 0;
            height: 2px;
            background-color: $green;
            transition: all .3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        }
        &:hover {
            &::after {
                width: 100%;
            }
        }
    }
}

.user-meta {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.user-meta_button-login {
    padding-right: 2.5rem;
    margin-left: 5rem;
    margin-right: 1rem;
}

.user-meta_phone {
    color: $black;
    text-decoration: none;
}

.user-meta_button  {
    background: transparent;
    border: none;
    @include paragraphNormal;
    margin-left: 2.75rem;
    cursor: pointer;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: -2rem;
        transform: translateY(-50%);
        width: 2rem;
        height: 1rem;
    }
    &--price {
        &::after {
            background: url(../img/icons/price.svg) center center no-repeat;
            background-size: contain;
        }
       
    }
    &--calc {
        &::after {
            background: url(../img/icons/calc.svg) center center no-repeat;
            background-size: contain;
        }
        
    }
}