



.button {
  text-decoration: none;
  background-color: #ffffff;
  border-radius: 16px;
  padding: 0.685rem;
  color: #000000;
  display: inline-block;
  cursor: pointer;
  border: none;
}
.button--login {
  position: relative;
}
.button--login::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0.5rem;
  transform: translateY(-50%);
  background: url(../img/icons/login.svg) center center no-repeat;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
}
.button--checkout {
  position: relative;
  padding: 1.3rem;
}
.button--checkout::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(../img/icons/checkout.svg) center center no-repeat;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
}
.button--arrow {
  position: relative;
  padding-right: 2.5rem;
  color: #393D47;
}
.button--arrow::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0.5rem;
  transform: translateY(-50%);
  background: url(../img/icons/arrow.svg) center center no-repeat;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#main {
  position: relative;
  z-index: 2;
  min-height: 85vh;
}

img, video {
  max-width: 100%;
  height: auto;
}

pre {
  white-space: pre-line;
  padding-top: 1rem;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
  font-family: "Roboto";
}

body {
  font-family: "Formular", sans-serif;
  background-color: #F0EBE7;
}

img, svg {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: "Formular";
  src: url("../fonts/Formular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Geometria";
  src: url("../fonts/Geometria-Regular.ttf") format("truetype");
  font-style: normal;
}
.main-footer {
  background-color: #393D47;
}

.main-footer_body {
  border-bottom: 1px solid #929396;
}

.main-footer_body-wrap {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  padding: 1.875rem 2.5rem;
}

.main-footer_body-logo {
  grid-column: 1/3;
}
.main-footer_body-logo img {
  width: 100%;
  object-fit: contain;
}

.main-footer_body-contacts {
  grid-column: 4/7;
}

.main-footer_body-contacts-links {
  font-size: 2.5rem;
  line-height: 2.5rem;
  font-family: "Geometria";
  color: #ffffff;
  text-decoration: none;
  display: inline-block;
}

.main-footer_body-contacts-requisites {
  font-size: 1rem;
  line-height: 1.25rem;
  color: #929396;
  max-width: 29rem;
  margin-top: 2.5rem;
}

.main-footer_body-options {
  grid-column: 8/13;
}
.main-footer_body-options ul {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem 1.5rem;
  align-items: center;
  list-style: none;
}
.main-footer_body-options ul a {
  text-decoration: none;
  color: #ffffff;
}
.main-footer_body-options li {
  width: max-content;
}

.main-footer_body-options-time {
  margin-top: 6rem;
}
.main-footer_body-options-time p {
  color: #929396;
}

.main-footer_body-options-payments {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.main-footer_body-options-payments a:not(:first-of-type) {
  margin-left: 1rem;
}

.main-footer_copywrite {
  padding: 1.875rem 2.5rem;
}
.main-footer_copywrite p {
  color: #ffffff;
}

.main-footer_copywrite-quote {
  font-size: 2.5rem;
  line-height: 2.5rem;
  font-family: "Geometria";
  max-width: 60rem;
  margin-bottom: 1rem;
}
.main-header {
  position: sticky;
  top: 0;
  z-index: 9;
  padding: 1.875rem 2.5rem;
}

.main-header_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header_wrapper-logo {
  max-width: 16.5rem;
}
.main-header_wrapper-logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-header_wrapper-menu ul {
  list-style: none;
  display: flex;
  align-items: center;
}
.main-header_wrapper-menu ul a {
  text-decoration: none;
  color: #000000;
}
.main-header_wrapper-menu li {
  position: relative;
}
.main-header_wrapper-menu li:not(:first-of-type) {
  margin-left: 1.5rem;
}
.main-header_wrapper-menu li::after {
  content: "";
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #2B9500;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.main-header_wrapper-menu li:hover::after {
  width: 100%;
}

.user-meta {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.user-meta_button-login {
  padding-right: 2.5rem;
  margin-left: 5rem;
  margin-right: 1rem;
}

.user-meta_phone {
  color: #000000;
  text-decoration: none;
}

.user-meta_button {
  background: transparent;
  border: none;
  font-size: 1rem;
  line-height: 1.25rem;
  margin-left: 2.75rem;
  cursor: pointer;
  position: relative;
}
.user-meta_button::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -2rem;
  transform: translateY(-50%);
  width: 2rem;
  height: 1rem;
}
.user-meta_button--price::after {
  background: url(../img/icons/price.svg) center center no-repeat;
  background-size: contain;
}
.user-meta_button--calc::after {
  background: url(../img/icons/calc.svg) center center no-repeat;
  background-size: contain;
}
.block-slider {
  height: 100vh;
}

.block-slider_text {
  position: absolute;
  top: 0;
  left: 0;
  padding: 2.85rem;
}

.block-slider_text-title {
  font-size: 4.25rem;
  line-height: 4.6875rem;
  font-family: "Geometria";
  max-width: 70rem;
}

.block-slider_button {
  font-size: 1.125rem;
  margin-top: 2.75rem;
}

.swiper-pagination-bullets {
  display: flex;
  align-items: center;
}

.block-slider-bullet {
  width: 100%;
  border-radius: 16px;
  height: 5px;
  background-color: #929396;
  position: relative;
  overflow: hidden;
}
.block-slider-bullet .block-slider-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  opacity: 1;
}
.block-slider-bullet.swiper-pagination-bullet-active .block-slider-bg {
  animation: slide-progress 2.8s linear;
  animation-fill-mode: forwards;
}

.block-slider_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@keyframes slide-progress {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@media screen and (min-width: 320px) and (max-width: 768px) {
  html {
    font-size: 16px;
  }
}
@media only screen and (min-width: 1280px) and (max-width: 1600px) {
  html {
    font-size: 0.95vw;
  }
}
@media only screen and (min-width: 1600px) and (max-width: 1920px) {
  html {
    font-size: 0.85vw;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1280px) {
  html {
    font-size: 16px;
  }
}