@import '../mixins/mixin-main.scss';
.block-slider {
    height: 100vh;
}

.block-slider_text {
    position: absolute;
    top: 0;
    left: 0;
    padding: 2.85rem;
}

.block-slider_text-title {
    @include h1;
    max-width: 70rem;
}

.block-slider_button {
    font-size: 1.125rem;
    margin-top: 2.75rem;
}

.swiper-pagination-bullets {
    display: flex;
    align-items: center;
}

.block-slider-bullet {
    width: 100%;
    border-radius: 16px;
    height: 5px;
    background-color: $gray;
    position: relative;
    overflow: hidden;
    .block-slider-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $white;
        opacity: 1;
    }
    &.swiper-pagination-bullet-active {
        .block-slider-bg {
            animation: slide-progress 2.8s linear;
            animation-fill-mode: forwards;
        }
    }
}

.block-slider_image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@keyframes slide-progress {
    0% {
        transform: translateX(-100%);
        // width: 0;
    }

    100% {
        transform: translateX(0);
        // width: 100%;
    }
}