@import '../../mixins/mixin-main.scss';
.main-footer {
    background-color: $darkgray;
    
}

.main-footer_body {
    border-bottom: 1px solid $gray;
}

.main-footer_body-wrap {
    display: grid;
    grid-template-columns: repeat(12,1fr);
    padding: 1.875rem 2.5rem;
    
}

.main-footer_body-logo {
    grid-column: 1/3;
    img {
        width: 100%;object-fit: contain;
    }
}

.main-footer_body-contacts {
    grid-column: 4/7
}

.main-footer_body-contacts-links {
    @include h3;
    color: $white;
    text-decoration: none;
    display: inline-block;
}

.main-footer_body-contacts-requisites {
    @include paragraphNormal;
    color: $gray;
    max-width: 29rem;
    margin-top: 2.5rem;
}

.main-footer_body-options {
    grid-column: 8/13;
    ul {
        display: flex;
        flex-wrap: wrap;
        grid-gap: 1rem 1.5rem;
        align-items: center;
        list-style: none;
        a {
            text-decoration: none;
            color: $white;
        }
    }
    li {
        width: max-content;
    }
}
.main-footer_body-options-time {
    margin-top: 6rem;
    p {
        color: $gray;
    }
}

.main-footer_body-options-payments {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    a {
        &:not(:first-of-type) {
            margin-left: 1rem;
        }
    }
}

.main-footer_copywrite {
    padding: 1.875rem 2.5rem;
    p {
        color: $white
    }
}

.main-footer_copywrite-quote {
    @include h3;
    max-width: 60rem;
    margin-bottom: 1rem;
}