@mixin button {
    text-decoration: none;
    background-color: $white;
    border-radius: 16px;
    padding: .685rem;
    color: $black;
    display: inline-block;
    cursor: pointer;
    border: none;
    &--login {
        position: relative;
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            right: .5rem;
            transform: translateY(-50%);
            background: url(../img/icons/login.svg) center center no-repeat;
            background-size: contain;
            width: 1.5rem;
            height: 1.5rem;
        }
        
    }
    &--checkout {
        position: relative;
        padding: 1.3rem;
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: url(../img/icons/checkout.svg) center center no-repeat;
            background-size: contain;
            width: 1.5rem;
            height: 1.5rem;
        }
    }
    &--arrow {
        position: relative;
        padding-right: 2.5rem;
        color: $darkgray;
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            right: .5rem;
            transform: translateY(-50%);
            background: url(../img/icons/arrow.svg) center center no-repeat;
            background-size: contain;
            width: 1.5rem;
            height: 1.5rem;
        }
    }
}